.App {
  min-height: 100vh;
  background: skyblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.slick-app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 16px 0;
}

.slick-app-heading {
  color: #ffffff;
  align-self: flex-start;
  margin-left: 12%;
  margin-bottom: 16px;
}

.poster {
  margin: 0px 10px;
  height: 50px;
  width: 90%;
  border-radius: 16px;
}

@media (min-width: 768px) {
  .poster {
    margin: 0px 15px;
    height: 200px;
  }
}

@media (min-width: 1024px) {
  .poster {
    margin: 0px 30px;
    height: 250px;
  }
}
